import React from 'react';

function MainMenu()
{
    return(
        
        <nav className="navbar" style={navbarStyle}>

            <a href="/" className="nav-item" style={homeStyle}>Home</a>
            <a href="/Login" className="nav-item" style={loginStyle}>Login</a>
            
            <button href="#" className="btn btn-default nav-item" style={searchStyle} >
                <i className="material-icons">search</i>
            </button>
        </nav>
    );
}

const navbarStyle = {
    width :'100%',
    // backgroundColor: 'white',
    // boxShadow: '#555555 0px 5px 20px',
    position :'fixed',
    display:'grid',
    gridTemplateColumns  :'5fr 1fr 1fr 1fr'
}
const homeStyle = {
    marginRight : '10px',
    marginLeft : '10px',
    gridColumnStart:'2',
    gridColumnEnd:"3"
    
}
const loginStyle = {
    marginRight : '10px',
    marginLeft : '10px',
    gridColumnStart:'3',
    gridColumnEnd:"4"
    
}
const searchStyle = {
    color: 'blue',
    padding:"10px",
    paddingBottom:'0px',
    gridColumnStart:'4',
    gridColumnEnd:"5"
}

export default MainMenu;