import React from 'react';
import axios from 'axios';

class Footer extends React.Component
{
    
    state = {
        nom_ville :[]
    }

    componentDidMount() {
            // Make a request for a user with a given ID
        // axios.get('https://my-json-server.typicode.com/mazmazigh/fakeDB/ville')
        axios.get('http://localhost/PHP_API/get_ville.php?nom_ville=Bejaia')
        .then(response => this.setState({nom_ville: response.data}))

    }
    
render(){

    return(
    <footer style={footerStyle} className="">
       <ul>
           <li>Adresse : Akbou-Bejaia</li>
           <li>Tel: 000000000</li>
           <li>email : contact@infotoolssolutions.com</li>
           <li>{this.state.nom_ville}</li>
       </ul>
    </footer>
    );
}
   
    

    
}

const footerStyle={
    backgroundColor: '#DDDDDD',
    
    gridArea: 'footer'
    
}
export default Footer;