import React from 'react';
import logo from "./../Media/en-tete.png";


function Header()
{
    return(
        <header style={headerStyle} className="">
           
           <img className="img-fluid" src = {logo} alt="en-tete"></img>
        </header>
    );
}


// const headerStyle ={
//     backgroundColor: 'white',
//     width: '100%',
//     display: 'block'
// }
const headerStyle = {
    gridArea : 'header'
}
export default Header;
