import React from 'react';

function Body()
{
    return(
        <React.Fragment>
            <div className="card" style={proStyle}>
                <div className="card-header">
                </div>
                <div className="card-body">
                    <h5 className="card-title">Je suis un professionnel</h5>
                    <p className="card-text">
                        Se connecter<br/>
                        Editer mon profile <br/>
                        Gerer mon calendrier<br/>
                    </p>
                    <a href="#" className="btn btn-primary">Commencer</a>
                </div>
            </div>

            <div className="card" style={userStyle}>
                <div className="card-header">

                </div>
                <div className="card-body">
                    <h5 className="card-title">Prendre un rendez-vous</h5>
                    <p className="card-text">
                        Se connecter <br/>
                        Trouver un professionnel<br/>
                        Reserver un crenau<br/>
                    </p>
                    <a href="#" className="btn btn-primary">Commencer</a>
                </div>
            </div>
        </React.Fragment>
    );
}

const proStyle={
    gridArea: 'pro'
}
const userStyle={
    gridArea: 'user'
}
export default Body;